import { Button } from "antd";
import React, { FC } from "react";
import { Helmet } from "react-helmet";
import Order from "../components/Basket/Order";
import { useActionCreators } from "../hooks/useAC";
import { UseTypedSelector } from "../hooks/useTypedSelector";

const Basket: FC = () => {
  const { orders, totalPrice } = UseTypedSelector((state) => state.Basket);
  const { acceptOrders } = useActionCreators();
  const accept = () => {
    acceptOrders();
  };
  const storage = localStorage.getItem("orders") || "[]";
  const jsonOrders = JSON.parse(storage);

  return (
    <>
      {" "}
      <Helmet defer={false} onChangeClientState={(state) => console.log(state)}>
        <meta name="title" content="Basket" />
        <meta name="description" content="Best games" />

        <meta property="og:type" content="website" />
        <meta
          property="og:url"
          content="https://liu1331.github.io/test.github.io-/basket"
        />
        <meta property="og:title" content="Basket" />
        <meta property="og:description" content="Best games" />
        <meta
          property="og:image"
          content="https://cdn.pasino.com/user_uploads/63b09ed0382e.jpeg"
        />

        <meta property="twitter:card" content="summary_large_image" />
        <meta
          property="twitter:url"
          content="https://liu1331.github.io/test.github.io-/basket"
        />
        <meta property="twitter:title" content="Basket" />
        <meta property="twitter:description" content="Best games" />
        <meta
          property="twitter:image"
          content="https://cdn.pasino.com/user_uploads/63b09ed0382e.jpeg"
        />
      </Helmet>{" "}
      <div className="Basket_content container">
        <div className="Orders_flex">
          {orders.length > 0 ? (
            orders.map((order) => <Order key={order.productName} {...order} />)
          ) : (
            <div
              style={{
                width: "100%",
                height: "100%",
                textAlign: "center",
                paddingTop: "30%",
              }}
            >
              <h1> У вас нету товаров в корзине:(</h1>
            </div>
          )}
          <div className="Order_item">
            <div className="Order_item__title">
              {jsonOrders.length > 0 ? (
                <h1 style={{ color: "goldenrod" }}>
                  {"Сума к оплате: $" + totalPrice}
                </h1>
              ) : null}
            </div>
            <div>
              {jsonOrders.length > 0 ? (
                <Button
                  size="large"
                  style={{ marginRight: "25px" }}
                  onClick={() => accept()}
                  danger
                >
                  Оформить заказ
                </Button>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Basket;
